html{
    background-color: black;
}
.login{
    background-color:rgb(57, 70, 81);
    height:500px;
    width:550px;
    margin: auto;
    
    display:flex;
    flex-direction:column;
    justify-content:space-around;
    border-radius: 10px;
    margin-top:150px;
}
.website_name{
    
flex:0.05;
margin: auto;
font-size:50px;
color:white;
}
.login_title{
    flex:0.05;
    margin: auto;
    color:white;

    font-size:30px;
}
.login_other_methods{
    
    display:flex;
    flex-direction:column;
    justify-content:space-around;
}
.email_login{
    flex:0.38;
    display:flex;
    flex-direction:column;
    text-align: center;
    justify-content:space-around;
}

.no_account{
    flex:0.05;
}


.google_login{
width:340px;
height:40px;
margin:auto;
border: none;
border-radius:20px;
}
 .facebook_login{
    width:340px;
height:40px;
margin:auto;
border: none;
border-radius:20px;
 }
 .email_box{
    width:330px;
    height:65px;
    margin:auto;  
    text-align: center;
    background-color: rgba(98, 97, 95, 0.12);
    border: 0.5px solid white;
    border-radius:7px;
    color:azure;
    font-size: 20px;

 }
 
.forgot_password_button{
    width:340px;
height:40px;
    border-radius:20px;
    margin:auto;  
    border: none;
    border-radius:20px;
    background-color: rgb(12, 11, 11);
    font-size: 15px;
    font-weight: 700;
    color:azure;
    
    margin-top:5px;
}
.email_next_button{
    width:340px;
height:40px;
    margin:auto;  
    border: none;
    border-radius:20px;
    font-size: 19px;
    font-weight: 700;
    margin-top:5px;
}
.no_account{
    flex:0.2;
    text-align: center;
    margin-top: 40px;
    font-size: 20px;
    margin-bottom:30px;
    cursor:pointer;
}

.login_other_methods img{
    height:30px;
    width:30px;
}
.login_other_methods span{
position:relative;
top:-10px;
color:rgb(54, 53, 53);

margin-right: 10px;
font-weight: 800;
}

.email_login button{
    margin-top:20px;

}
